import { CoreProvider } from 'src/app/services/core';
import { Injectable, EventEmitter } from '@angular/core';

@Injectable({ providedIn: 'root' })
export class AuthServiceProvider {

  core: CoreProvider = null;

  user: any = null;
  onUser: EventEmitter<any> = new EventEmitter();
  token: any = null;

  constructor() { }

  initCore(core:CoreProvider) {
    this.core = core;
    const token = localStorage.getItem('sessToken');
    if (token) this.token = token;
    this.initChecks();
  }

  initChecks() {
    const errSession = () => {
      this.user = false;
      this.token = false;
      this.saveToken();
    };
    if (this.token) {
      this.core.http.get(this.core.relayEndpoint+'users/me', {headers:{
        Authorization: 'Bearer '+this.token,
        'x-browser-id': this.core.browserId
      }}).subscribe(res => {
        this.saveUser(res);
      }, err => {
        errSession();
      });
    } else {
      errSession();
    }
    // TODO: if refresh token try to refresh session
  }

  login(data, cbOk:Function=null, cbKo:Function=null) {
    this.core.http.post(this.core.relayEndpoint+'users/login', data, {headers:{
      'x-browser-id': this.core.browserId
    }}).subscribe((res:any) => {
      this.token = res.accessToken;
      this.saveToken();
      this.initChecks();
      if (cbOk) cbOk();
    }, err => {
      this.user = false;
      this.token = false;
      this.saveToken();
      if (cbKo) cbKo(err);
    });
  }

  logout() {

  }

  saveToken() {
    localStorage.setItem('sessToken', this.token);
  }
  saveUser(user) {
    this.user = user;
    this.onUser.next(this.user);
  }

}
